
import { ReactNode, useState, useEffect } from 'react';
import { useLocation, Link } from 'react-router-dom';
import { motion, AnimatePresence } from 'framer-motion';
import { useAuth } from '../contexts/AuthContext';
import { useFolders } from '../contexts/FolderContext';
import { 
  Home, Search, User, LogOut, Menu, X, ChevronRight, Heart, 
  Clock, Calendar, BookOpen, PlusCircle
} from 'lucide-react';
import { cn } from '@/lib/utils';
import { Button } from '@/components/ui/button';
import { 
  DropdownMenu, 
  DropdownMenuTrigger, 
  DropdownMenuContent, 
  DropdownMenuItem, 
  DropdownMenuLabel, 
  DropdownMenuSeparator 
} from '@/components/ui/dropdown-menu';
import { Avatar, AvatarImage, AvatarFallback } from '@/components/ui/avatar';
import { 
  Sheet, 
  SheetTrigger, 
  SheetContent, 
  SheetHeader, 
  SheetTitle, 
  SheetClose 
} from '@/components/ui/sheet';

interface LayoutProps {
  children: ReactNode;
  title?: string;
  hideNavigation?: boolean;
}

const Layout = ({ children, title, hideNavigation = false }: LayoutProps) => {
  const { user, logout } = useAuth();
  const location = useLocation();
  const { getUserFolders } = useFolders();
  const [isMounted, setIsMounted] = useState(false);
  const [scrolled, setScrolled] = useState(false);
  const userFolders = getUserFolders();

  useEffect(() => {
    const handleScroll = () => {
      setScrolled(window.scrollY > 10);
    };

    window.addEventListener('scroll', handleScroll);
    setIsMounted(true);
    
    return () => {
      window.removeEventListener('scroll', handleScroll);
    };
  }, []);

  if (!isMounted) {
    return null;
  }

  const navItems = [
    { icon: Home, label: 'Home', path: '/' },
    { icon: Search, label: 'Search', path: '/search' },
    { icon: BookOpen, label: 'Bookmarks', path: '/bookmarks' },
    { icon: User, label: 'Profile', path: '/profile' },
  ];

  const getInitials = (name: string) => {
    return name
      .split(' ')
      .map(n => n[0])
      .join('')
      .toUpperCase();
  };

  return (
    <div className="min-h-screen flex flex-col bg-background">
      <header 
        className={cn(
          "fixed top-0 left-0 right-0 z-30 transition-all duration-300 border-b",
          scrolled 
            ? "bg-background/80 backdrop-blur-md border-border/50 py-2" 
            : "bg-transparent border-transparent py-4"
        )}
      >
        <div className="container mx-auto px-4 flex justify-between items-center">
          <Link to="/" className="flex items-center space-x-2">
            <span className="text-primary">
              <BookOpen className="h-6 w-6" />
            </span>
            <span className="font-display text-xl font-semibold">Savory</span>
          </Link>

          {!hideNavigation && (
            <nav className="hidden md:flex items-center space-x-2">
              {navItems.map((item) => (
                <Link
                  key={item.path}
                  to={item.path}
                  className={cn(
                    "relative px-4 py-2 rounded-md flex items-center space-x-1 transition-colors",
                    location.pathname === item.path
                      ? "text-primary font-medium"
                      : "text-muted-foreground hover:text-foreground hover:bg-secondary"
                  )}
                >
                  <item.icon className="w-4 h-4" />
                  <span>{item.label}</span>
                  {location.pathname === item.path && (
                    <motion.div
                      layoutId="nav-indicator"
                      className="absolute bottom-0 left-0 right-0 h-0.5 bg-primary rounded-full"
                      transition={{ type: "spring", bounce: 0.2, duration: 0.6 }}
                    />
                  )}
                </Link>
              ))}
            </nav>
          )}

          <div className="flex items-center space-x-4">
            {!hideNavigation && (
              <Sheet>
                <SheetTrigger asChild>
                  <Button variant="ghost" size="icon" className="md:hidden">
                    <Menu className="h-5 w-5" />
                    <span className="sr-only">Menu</span>
                  </Button>
                </SheetTrigger>
                <SheetContent side="left" className="p-0">
                  <SheetHeader className="p-4 border-b">
                    <SheetTitle className="font-display text-xl">Savory</SheetTitle>
                  </SheetHeader>
                  <div className="py-4">
                    <nav className="space-y-1">
                      {navItems.map((item) => (
                        <SheetClose asChild key={item.path}>
                          <Link
                            to={item.path}
                            className={cn(
                              "flex items-center px-4 py-3 w-full",
                              location.pathname === item.path
                                ? "bg-secondary text-foreground font-medium"
                                : "text-muted-foreground"
                            )}
                          >
                            <item.icon className="mr-3 h-5 w-5" />
                            {item.label}
                            {location.pathname === item.path && (
                              <ChevronRight className="ml-auto h-4 w-4" />
                            )}
                          </Link>
                        </SheetClose>
                      ))}
                    </nav>

                    <div className="px-4 pt-6 pb-2">
                      <div className="text-xs font-medium text-muted-foreground uppercase tracking-wider">
                        Bookmark Folders
                      </div>
                    </div>
                    <nav className="space-y-1">
                      {userFolders.map((folder) => {
                        const FolderIcon = folder.icon === 'heart' 
                          ? Heart 
                          : folder.icon === 'clock' 
                            ? Clock 
                            : folder.icon === 'calendar' 
                              ? Calendar 
                              : BookOpen;
                        
                        return (
                          <SheetClose asChild key={folder.id}>
                            <Link
                              to={`/bookmarks?folder=${folder.id}`}
                              className="flex items-center px-4 py-2 text-muted-foreground"
                            >
                              <FolderIcon className="mr-3 h-4 w-4" style={{ color: folder.color }} />
                              {folder.name}
                            </Link>
                          </SheetClose>
                        );
                      })}
                      <SheetClose asChild>
                        <Link
                          to="/bookmarks?new=folder"
                          className="flex items-center px-4 py-2 text-muted-foreground"
                        >
                          <PlusCircle className="mr-3 h-4 w-4" />
                          Add new folder
                        </Link>
                      </SheetClose>
                    </nav>
                  </div>
                </SheetContent>
              </Sheet>
            )}

            <DropdownMenu>
              <DropdownMenuTrigger asChild>
                <Button 
                  variant="ghost" 
                  className="relative h-8 w-8 rounded-full border overflow-hidden focus-visible:ring-offset-0 focus-visible:ring-primary"
                >
                  <Avatar className="h-8 w-8">
                    {user?.avatar ? (
                      <AvatarImage src={user.avatar} alt={user.name} />
                    ) : (
                      <AvatarFallback>
                        {user?.name ? getInitials(user.name) : 'U'}
                      </AvatarFallback>
                    )}
                  </Avatar>
                </Button>
              </DropdownMenuTrigger>
              <DropdownMenuContent align="end" className="w-56">
                <DropdownMenuLabel>
                  <div className="flex flex-col space-y-1">
                    <p className="text-sm font-medium leading-none">{user?.name}</p>
                    <p className="text-xs leading-none text-muted-foreground">
                      @{user?.username}
                    </p>
                  </div>
                </DropdownMenuLabel>
                <DropdownMenuSeparator />
                <DropdownMenuItem asChild>
                  <Link to="/profile" className="cursor-pointer w-full">
                    Profile settings
                  </Link>
                </DropdownMenuItem>
                <DropdownMenuItem asChild>
                  <Link to="/bookmarks" className="cursor-pointer w-full">
                    My bookmarks
                  </Link>
                </DropdownMenuItem>
                <DropdownMenuSeparator />
                <DropdownMenuItem 
                  className="text-destructive focus:text-destructive cursor-pointer"
                  onClick={logout}
                >
                  <LogOut className="mr-2 h-4 w-4" />
                  <span>Log out</span>
                </DropdownMenuItem>
              </DropdownMenuContent>
            </DropdownMenu>
          </div>
        </div>
      </header>

      <main className="flex-grow pt-16 md:pt-20">
        {title && (
          <div className="container px-4 py-6">
            <h1 className="text-3xl md:text-4xl font-display font-semibold">{title}</h1>
          </div>
        )}
        <AnimatePresence mode="wait">
          <motion.div
            key={location.pathname}
            initial={{ opacity: 0, y: 10 }}
            animate={{ opacity: 1, y: 0 }}
            exit={{ opacity: 0, y: -10 }}
            transition={{ duration: 0.3 }}
            className="min-h-[calc(100vh-80px)]"
          >
            {children}
          </motion.div>
        </AnimatePresence>
      </main>

      <footer className="border-t bg-card py-4">
        <div className="container mx-auto px-4">
          <p className="text-center text-sm text-muted-foreground">
            Supharat Saelee @2024
          </p>
        </div>
      </footer>

      {!hideNavigation && (
        <div className="md:hidden fixed bottom-0 left-0 right-0 bg-card border-t z-20">
          <div className="flex justify-around">
            {navItems.map((item) => (
              <Link
                key={item.path}
                to={item.path}
                className={cn(
                  "flex flex-col items-center py-3 px-5 transition-colors",
                  location.pathname === item.path
                    ? "text-primary"
                    : "text-muted-foreground"
                )}
              >
                <item.icon className="h-5 w-5" />
                <span className="text-xs mt-1">{item.label}</span>
              </Link>
            ))}
          </div>
        </div>
      )}
    </div>
  );
};

export default Layout;
