export const csvApi = {
  getFiles: async () => {
    // Mock files from localStorage
    const stored = localStorage.getItem('mock_csv_files') || '[]';
    return JSON.parse(stored);
  },
  readFile: async (filename: string) => {
    // Mock read from localStorage
    const stored = localStorage.getItem(`mock_csv_${filename}`) || '';
    return { data: parseCSV(stored) };
  },
  deleteFile: async (filename: string) => {
    // Mock delete
    localStorage.removeItem(`mock_csv_${filename}`);
    return { success: true };
  },
  quickImport: async (file: File, dataType: 'recipes' | 'reviews' | 'users', append: boolean = false) => {
    // Mock import - parse and store locally
    const reader = new FileReader();
    reader.onload = async (e) => {
      const csvText = e.target?.result as string;
      const parsed = parseCSV(csvText);
      localStorage.setItem(`mock_csv_${file.name}`, csvText);
      localStorage.setItem('mock_csv_files', JSON.stringify([{ filename: file.name, size: file.size, uploadedAt: new Date().toISOString() }]));
      return { success: true, data: parsed };
    };
    reader.readAsText(file);
    return { success: true };
  },
  getFileUrl: (filename: string) => null, // No real URL in mock
};
