
import { useEffect, useState } from 'react';
import { csvApi } from '@/services/api';
import Papa from 'papaparse'; // Assume added dependency or use built-in
import Layout from '@/components/Layout';
import CsvUploadForm from '@/components/CsvUploadForm';
import { Button } from '@/components/ui/button';
import { Card, CardContent, CardHeader, CardTitle } from '@/components/ui/card';
import { Trash, Eye, FileText } from 'lucide-react';
import { toast } from 'sonner';
import { Table, TableHeader, TableRow, TableHead, TableBody, TableCell } from '@/components/ui/table';
import { Alert, AlertDescription } from '@/components/ui/alert';

type CsvFile = {
  filename: string;
  size: number;
  uploadedAt: string;
};

const CsvManagementPage = () => {
  const [files, setFiles] = useState<CsvFile[]>([]);
  const [isLoading, setIsLoading] = useState(true);
  const [selectedFile, setSelectedFile] = useState<string | null>(null);
  const [fileData, setFileData] = useState<any[] | null>(null);
  const [fileHeaders, setFileHeaders] = useState<string[]>([]);
  const [error, setError] = useState<string | null>(null);

  const fetchFiles = async () => {
    setIsLoading(true);
    setError(null);
    try {
      const data = await csvApi.getFiles();
      setFiles(data);
    } catch (error) {
      console.error('Error fetching files:', error);
      setError('Using local storage for files.');
      toast.info('Using local storage for CSV management');
    } finally {
      setIsLoading(false);
    }
  };

  useEffect(() => {
    fetchFiles();
  }, []);

  const handleDeleteFile = async (filename: string) => {
    try {
      await csvApi.deleteFile(filename);
      toast.success('File deleted successfully');
      setFiles(files.filter(file => file.filename !== filename));
      
      if (selectedFile === filename) {
        setSelectedFile(null);
        setFileData(null);
        setFileHeaders([]);
      }
    } catch (error) {
      console.error('Error deleting file:', error);
      toast.error('Failed to delete file');
    }
  };

  const handleViewFile = async (filename: string) => {
    setIsLoading(true);
    setError(null);
    try {
      const response = await csvApi.readFile(filename);
      
      if (!response || !response.data || !Array.isArray(response.data)) {
        throw new Error('Invalid data format');
      }
      
      setSelectedFile(filename);
      setFileData(response.data);
      
      if (response.data && response.data.length > 0) {
        setFileHeaders(Object.keys(response.data[0]));
      } else {
        setFileHeaders([]);
        toast.info('The file contains no data');
      }
      
    } catch (error) {
      console.error('Error viewing file:', error);
      setError(`Failed to read file "${filename}".`);
      toast.error('Failed to read file');
    } finally {
      setIsLoading(false);
    }
  };

  const handleViewRawFile = (filename: string) => {
    const fileUrl = csvApi.getFileUrl(filename);
    if (fileUrl) {
      window.open(fileUrl, '_blank');
    } else {
      toast.info('Raw file download not supported in standalone mode. Use local storage.');
    }
  };

  const handleUploadSuccess = (data?: any) => {
    fetchFiles();
    if (data) {
      setFileData(data);
      setFileHeaders(data.length > 0 ? Object.keys(data[0]) : []);
    }
  };

  const formatFileSize = (bytes: number) => {
    if (bytes < 1024) return bytes + ' bytes';
    if (bytes < 1024 * 1024) return (bytes / 1024).toFixed(1) + ' KB';
    return (bytes / (1024 * 1024)).toFixed(1) + ' MB';
  };

  const formatDate = (dateString: string) => {
    return new Date(dateString).toLocaleString();
  };

  return (
    <Layout>
      <div className="container mx-auto py-8">
        <h1 className="text-3xl font-bold mb-8">CSV Data Management</h1>
        
        <div className="grid md:grid-cols-2 gap-8">
          <div>
            <CsvUploadForm onSuccess={handleUploadSuccess} />
          </div>
          
          <div>
            <Card>
              <CardHeader>
                <CardTitle>Uploaded Files</CardTitle>
              </CardHeader>
              <CardContent>
                {error && (
                  <Alert variant="destructive" className="mb-4">
                    <AlertDescription>{error}</AlertDescription>
                  </Alert>
                )}
                
                {isLoading && !fileData ? (
                  <p className="text-center py-4">Loading...</p>
                ) : files.length === 0 ? (
                  <p className="text-center py-4 text-muted-foreground">No files uploaded yet</p>
                ) : (
                  <div className="space-y-4">
                    {files.map((file) => (
                      <div 
                        key={file.filename} 
                        className="flex items-center justify-between p-3 border rounded-md"
                      >
                        <div>
                          <p className="font-medium">{file.filename}</p>
                          <p className="text-sm text-muted-foreground">
                            {formatFileSize(file.size)} • Uploaded {formatDate(file.uploadedAt)}
                          </p>
                        </div>
                        <div className="flex space-x-2">
                          <Button 
                            variant="outline" 
                            size="icon"
                            title="View as Table" 
                            onClick={() => handleViewFile(file.filename)}
                          >
                            <Eye className="h-4 w-4" />
                          </Button>
                          <Button 
                            variant="outline" 
                            size="icon"
                            title="View Raw CSV" 
                            onClick={() => handleViewRawFile(file.filename)}
                          >
                            <FileText className="h-4 w-4" />
                          </Button>
                          <Button 
                            variant="ghost" 
                            size="icon"
                            title="Delete File" 
                            onClick={() => handleDeleteFile(file.filename)}
                          >
                            <Trash className="h-4 w-4" />
                          </Button>
                        </div>
                      </div>
                    ))}
                  </div>
                )}
              </CardContent>
            </Card>
          </div>
        </div>
        
        {fileData && fileData.length > 0 && (
          <div className="mt-8">
            <Card>
              <CardHeader>
                <CardTitle>
                  {selectedFile ? `Data Preview: ${selectedFile}` : 'Imported Data Preview'}
                </CardTitle>
              </CardHeader>
              <CardContent>
                <div className="overflow-x-auto">
                  <Table>
                    <TableHeader>
                      <TableRow>
                        {fileHeaders.map((header) => (
                          <TableHead key={header}>{header}</TableHead>
                        ))}
                      </TableRow>
                    </TableHeader>
                    <TableBody>
                      {fileData.slice(0, 10).map((row, index) => (
                        <TableRow key={index}>
                          {fileHeaders.map((header) => (
                            <TableCell key={`${index}-${header}`}>
                              {row[header]}
                            </TableCell>
                          ))}
                        </TableRow>
                      ))}
                    </TableBody>
                  </Table>
                  {fileData.length > 10 && (
                    <p className="text-center text-sm text-muted-foreground mt-4">
                      Showing 10 of {fileData.length} records
                    </p>
                  )}
                </div>
              </CardContent>
            </Card>
          </div>
        )}
      </div>
    </Layout>
  );
};

export default CsvManagementPage;
