import { useState } from 'react';
import { useRecipes } from '@/contexts/RecipeContext';
import Layout from '@/components/Layout';
import { Tabs, TabsContent, TabsList, TabsTrigger } from '@/components/ui/tabs';
import RecipeGrid from '@/components/RecipeGrid';
import { Card, CardContent, CardHeader, CardTitle } from '@/components/ui/card';

const categories = ['All', 'Breakfast', 'Lunch', 'Main Course', 'Dessert', 'Salad', 'Side Dish', 'Soup', 'Snack', 'Frozen Desserts'];

const CommunityPage = () => {
  const { recipes, isLoading } = useRecipes();
  const [activeTab, setActiveTab] = useState('All');

  const filteredRecipes = activeTab === 'All' 
    ? recipes 
    : recipes.filter(recipe => recipe.RecipeCategory === activeTab);

  if (isLoading) {
    return (
      <Layout title="Community">
        <div className="container py-8">
          <p>Loading recipes...</p>
        </div>
      </Layout>
    );
  }

  return (
    <Layout title="Community">
      <div className="container py-8">
        <div className="max-w-7xl mx-auto">
          <Card>
            <CardHeader>
              <CardTitle className="text-3xl font-bold">Recipe Community</CardTitle>
              <p className="text-muted-foreground">Browse recipes shared by our community, organized by category.</p>
            </CardHeader>
            <CardContent>
              <Tabs value={activeTab} onValueChange={setActiveTab} className="w-full">
                <TabsList className="grid w-full grid-cols-2 md:grid-cols-3 lg:grid-cols-5">
                  {categories.map(category => (
                    <TabsTrigger key={category} value={category} className="capitalize">
                      {category}
                    </TabsTrigger>
                  ))}
                </TabsList>
                {categories.map(category => (
                  <TabsContent key={category} value={category} className="pt-6">
                    {category === activeTab && (
                      <div>
                        {filteredRecipes.length > 0 ? (
                          <RecipeGrid recipes={filteredRecipes} />
                        ) : (
                          <p className="text-center text-muted-foreground py-8">
                            No recipes available in the "{category}" category yet. Check back soon!
                          </p>
                        )}
                      </div>
                    )}
                  </TabsContent>
                ))}
              </Tabs>
            </CardContent>
          </Card>
        </div>
      </div>
    </Layout>
  );
};

export default CommunityPage;